<template>
    <view>
        <view class="box">

            <!-- 昵称输入 -->
            <view class="box-in">
                <view class="box-in-in">
                    <input class="input-box-input" placeholder-style="color:#aaa;letter-spacing: 2rpx;" placeholder="请输入昵称"
                        :type="nicknameInputType" @blur="blur" :cursor-spacing="$kt.util.rpxToPx(30)" maxlength="10"
                        v-model="requestParam.nickname" />
                </view>
            </view>

            <!-- 选择生日 -->
            <birthday @change="birthdayChange"></birthday>

            <!-- 性别选择 -->
            <view class="box-in">
                <view :style="{
                    'opacity': requestParam.gender == '' ? '.5' : '1',
                }" class="box-in-in-2-title">
                    选择性别
                </view>
                <view class="box-in-in-2"
                    :style="'float: left;' + (requestParam.gender == '0' ? 'border:5rpx solid #ff6781' : '')"
                    @click="checkGender('0')">
                    <view style="height: 10rpx;"></view>
                    <image class="box-in-in-icon" :style="{
                        'opacity': requestParam.gender == '0' ? '1' : '.5'
                    }" :src="$kt.file.byPath('/image/icon/genderWoman.svg')"></image>
                </view>

                <view class="box-in-in-2"
                    :style="'float: right;' + (requestParam.gender == 1 ? 'border:5rpx solid #1296db' : '')"
                    @click="checkGender('1')">

                    <view style="height: 10rpx;"></view>
                    <image class="box-in-in-icon" :style="{
                        'opacity': requestParam.gender == 1 ? '1' : '.5'

                    }" :src="$kt.file.byPath('/image/icon/genderMan.svg')"></image>
                </view>
            </view>
            <view style="height: 100rpx;"></view>


            <!-- 性取向选择 -->
            <sexualOrientation @checkSexualOrientation="checkSexualOrientation"></sexualOrientation>


            <!-- 情感取向选择 -->
            <emotionalOrientation ref="emotionalOrientation" @checkEmotionalOrientationList="checkEmotionalOrientationList"
                @getByGenderAndSexualOrientationCodeFinally="getByGenderAndSexualOrientationCodeFinally">
            </emotionalOrientation>

            <sadomasochismAttr @change="sadomasochismAttrChange"></sadomasochismAttr>

            <view style="height: 10rpx;"></view>

            <!-- 自我介绍 -->
            <view class="box-in">
                <view class="box-in-in-2-title" :style="{
                    'opacity': requestParam.introduction.trim() == '' ? '.5' : '1'
                }">
                    自我介绍
                </view>
                <view style="position: relative;">
                    <view style="height: 5rpx;"></view>
                    <textarea style="height: 150rpx;
                        padding: 20rpx 20rpx 40rpx 20rpx;
                        box-sizing: border-box;
                        border-radius: 10rpx;
                        font-size: 30rpx;
                        color: #333;
                        width: calc(100% - 15rpx);
                        background-color: #f9f9f9;
                        text-align: left;
                        " @input="introductionInput" placeholder-style="color:#aaa;letter-spacing: 2rpx;"
                        placeholder="请输入自我介绍" maxlength="50" v-model="requestParam.introduction"></textarea>
                    <view style="
                    position: absolute;
                    right: 20rpx;
                    bottom: 5rpx;
                    font-size: 24rpx;
                    color: #aaa;

                    ">{{ requestParam.introduction.length }}{{ "/" }}50</view>
                </view>
            </view>
        </view>



    </view>
</template>

<script>
import sexualOrientation from './sexualOrientation.vue';
import emotionalOrientation from './emotionalOrientation.vue';
import birthday from './birthday.vue';
import sadomasochismAttr from './sadomasochismAttr.vue';


export default {
    components: {
        sexualOrientation,
        emotionalOrientation,
        birthday,
        sadomasochismAttr
    },
    data() {
        return {
            nicknameInputType: "",
            requestParam: {
                birthday: "",
                gender: "",
                nickname: "",
                introduction: "",
                sadomasochismAttrCode: "",
                sexualOrientationCode: "",
                emotionalOrientationCodeList: []
            },
        }
    },
    created() {
        // #ifndef MP-WEIXIN
        this.nicknameInputType = "text";
        // #endif
        // #ifdef MP-WEIXIN
        this.nicknameInputType = "nickname";
        // #endif
    },
    methods: {
        emotionalOrientationOpen() {
            if (this.requestParam.gender != '' && this.requestParam.sexualOrientationCode != '') {
                this.$refs.emotionalOrientation.open(this.requestParam.gender, this.requestParam.sexualOrientationCode);
                this.$emit("emotionalOrientationOpen");
            }
        },
        introductionInput(e) {
            this.requestParam.introduction = e.detail.value;
            this.$emit("change", this.requestParam);
        },
        blur(e) {
            this.requestParam.nickname = e.detail.value;
            console.log("blur", e);
            this.$emit("change", this.requestParam);
        },
        birthdayChange(birthday) {
            this.requestParam.birthday = birthday + "";
            this.$emit("change", this.requestParam);
        },
        sadomasochismAttrChange(sadomasochismAttrCode) {
            this.requestParam.sadomasochismAttrCode = sadomasochismAttrCode;
            this.$emit("change", this.requestParam);
        },
        checkGender(gender) {
            this.requestParam.gender = gender;
            this.$forceUpdate();
            this.emotionalOrientationOpen();
            this.$emit("change", this.requestParam);
        },
        checkSexualOrientation(sexualOrientationCode) {
            this.requestParam.sexualOrientationCode = sexualOrientationCode;
            this.emotionalOrientationOpen();
            this.$emit("change", this.requestParam);
        },
        checkEmotionalOrientationList(emotionalOrientationCodeList) {
            this.requestParam.emotionalOrientationCodeList = emotionalOrientationCodeList;
            this.$emit("change", this.requestParam);
        },
        getByGenderAndSexualOrientationCodeFinally() {
            this.$emit("getByGenderAndSexualOrientationCodeFinally");
        },
    }
}
</script>

<style lang="scss" scoped>
.box {
    padding: 30rpx 100rpx 30rpx 100rpx;
    box-sizing: border-box;
    text-align: center;
}

.box-in {
    margin-bottom: 30rpx;
    text-align: center;

    .box-in-title {
        font-size: 30rpx;
        font-weight: bold;
        color: #333;
        margin-bottom: 10rpx;
    }

    .box-in-in {
        background-color: #f5f5f5;
        border-radius: 10rpx;
        padding: 10rpx;

        .input-box-input {
            width: 100%;
            padding: 10rpx 0rpx 10rpx 0rpx;
            font-size: 30rpx;
            color: #333;
            border: none;
            outline: none;
            font-weight: bold;
            // 文字间距
            letter-spacing: 2rpx;
        }

    }

    .box-in-in-2-title {
        font-size: 30rpx;
        color: #666;
        text-align: left;
        margin-bottom: 20rpx;
        // margin-left: 10rpx;
    }

    .box-in-in-2 {
        width: calc(50% - 50rpx);
        display: inline-block;
        border: 5rpx solid #f0f0f0;
        padding: 10rpx;
        border-radius: 20rpx;

        .box-in-in-icon {
            width: 40rpx;
            height: 40rpx;
        }
    }

    .box-in-in-3 {
        border-radius: 10rpx;
        padding: 10rpx;
        margin-top: 30rpx;

        .box-in-in-3-title {
            font-size: 30rpx;
            font-weight: bold;
            color: #333;
            margin-bottom: 20rpx;
        }

        .box-in-in-3-list {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            align-items: center;
        }

        .box-in-in-3-list-item {
            width: calc(33.33% - 20rpx);
            margin-bottom: 20rpx;
            border: 5rpx solid #f0f0f0;
            padding: 10rpx;
            border-radius: 20rpx;
            text-align: center;
            box-sizing: border-box;
        }

        .box-in-in-3-list-item-text {
            font-size: 30rpx;
            font-weight: bold;
            color: #333;
            opacity: .5;
        }
    }

}

.checkot {
    font-size: 30rpx;
    margin-bottom: 10rpx;
    border: 5rpx solid #f0f0f0;
    text-align: left;
    padding: 20rpx;
    color: #666;
    border-radius: 20rpx;
}
</style>